@charset "UTF-8";
/*
    Reset 코드
*/
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.blind {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

body,
button,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
legend,
li,
ol,
p,
select,
table,
td,
textarea,
th,
ul {
  margin: 0;
  padding: 0;
}

body,
button,
input,
select,
table,
textarea {
  font-family: -apple-system, BlinkMacSystemFont, "Malgun Gothic", "맑은 고딕", helvetica, "Apple SD Gothic Neo", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  line-height: inherit;
}

textarea {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background-color: transparent;
  border: 0;
  word-break: keep-all;
  word-wrap: break-word;
}

button,
input {
  -webkit-border-radius: 0;
  border-radius: 0;
  border: 0;
}

button {
  background-color: transparent;
}

fieldset,
img {
  border: 0;
}

img {
  vertical-align: top;
}

ol,
ul {
  list-style: none;
}

address,
em {
  font-style: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

iframe {
  overflow: hidden;
  margin: 0;
  border: 0;
  padding: 0;
  vertical-align: top;
}

mark {
  background-color: transparent;
}

i {
  font-style: normal;
}

html,
body,
#wrap {
  min-width: 768px;
  height: 100%;
}

.card-area {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.card-area .card-content {
  width: auto;
  height: auto;
  margin: 5px;
  border: 1px solid #ececec;
  border-radius: 6px;
  background-color: transparent;
  text-align: center;
}

.card-area .card-content .card-info {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  padding: 10px;
  cursor: pointer;
}

.card-area .card-content .card-info h2 {
  font-size: 30px;
  color: #fff;
}

.card-area .card-content .card-info .back-card {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 750px;
  height: 650px;
  margin-top: -325px;
  margin-left: -375px;
  background-color: #fff;
  border-radius: 10px;
  transform: rotateY(90deg);
  -webkit-transform: rotateY(90deg);
  z-index: 100;
  cursor: default;
}

.card-area .card-content .card-info .back-card .card-detail {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0 60px;
  text-align: left;
}

.card-area .card-content .card-info .back-card .card-detail .info-data {
  display: table;
  table-layout: fixed;
  width: 100%;
  height: 100%;
}

.card-area .card-content .card-info .back-card .card-detail .info-data p {
  line-height: 26px;
  word-break: keep-all;
  word-wrap: break-word;
  white-space: normal;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 6;
}

.card-area .card-content .card-info .back-card .card-detail .info-data dl {
  display: table-cell;
  vertical-align: middle;
}

.card-area .card-content .card-info .back-card .card-detail .info-data dl dt {
  overflow: hidden;
  margin-top: 10px;
  font-size: 22px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.card-area .card-content .card-info .back-card .card-detail .info-data dl dt:first-child {
  margin-top: 0;
}

.card-area .card-content .card-info .back-card .card-detail .info-data dl dd {
  overflow: hidden;
  margin-top: 5px;
  font-size: 18px;
  color: #5e5e5e;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.card-area .card-content .card-info .back-card .card-detail .info-data dl dd ul li {
  overflow: hidden;
  position: relative;
  padding-left: 15px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.card-area .card-content .card-info .back-card .card-detail .info-data dl dd ul li:before {
  content: "-";
  position: absolute;
  top: 0;
  left: 0;
}

.card-area .card-content .card-info.rotate-card .front-card {
  opacity: 1;
  -webkit-animation: aniRotateCard 0.5s 1;
  animation: aniRotateCard 0.5s 1;
  -webkit-transform: rotateY(90deg);
  transform: rotateY(90deg);
}

.card-area .card-content .card-info.rotate-card .back-card {
  opacity: 1;
  -webkit-animation: aniRotateCard2 0.5s 0.5s 1 backwards;
  animation: aniRotateCard2 0.5s 0.5s 1 backwards;
  -webkit-transform: rotateY(0);
  transform: rotateY(0);
}

.container {
  position: relative;
  height: 100%;
  background: #11998e;
  background: -webkit-linear-gradient(to right, #38ef7d, #11998e);
  background: linear-gradient(to right, #38ef7d, #11998e);
}

.container .card-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 1280px;
  height: 100%;
  margin: 0 auto;
}

.container .card-box .real-content {
  position: relative;
}

.card-close {
  position: absolute;
  top: 30px;
  right: 30px;
  width: 30px;
  height: 30px;
  background: url(../img/close.svg) 0 0 no-repeat;
  background-size: cover;
  cursor: pointer;
}

/*
    Animation Keyframes
*/
@keyframes aniRotateCard {
  0% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(90deg);
  }
}

@keyframes aniRotateCard2 {
  0% {
    transform: rotateY(-90deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

@keyframes aniRotateBack {
  0% {
    transform: rotateY(90deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

.list_tab ul {
  display: flex;
  width: 100%;
  height: 40px;
  box-sizing: border-box;
}

.list_tab ul li {
  flex: 1;
  line-height: 40px;
  text-align: center;
  background-color: pink;
}

.list_tab ul li a {
  display: block;
  height: 100%;
  text-decoration: none;
}

.list_tab ul li .sub_tab {
  display: none;
}

.list_tab ul li .sub_tab li {
  position: relative;
  background-color: coral;
}

.list_tab ul li .sub_tab li ul {
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
}

.list_tab ul li .sub_tab li:nth-child(3) {
  background-color: red;
}

.list_tab ul li .sub_tab li:nth-child(3):hover ul {
  display: block;
}

.list_tab ul li:hover .sub_tab {
  display: block;
}

.list_tab ul li:last-child .sub_tab li:nth-child(3):hover ul {
  left: -100%;
}
